*{
    margin: 0;
    padding: 0;
}
a{
    text-decoration: none;
}
ul,li{
    list-style: none;
}
i,em{
    font-style: normal;
}
b,strong{
    font-weight: normal;
}
.clearfix{
    zoom: 1;
}
.clearfix:after{
    content: "";
    clear: both;
    display: block;
    height: 0;
    visibility: hidden;
}
input{
    outline: none;
}
textarea{
    outline: none;
}